import {
  USER_LOGIN_FAIL,
  USER_LOGIN_REQUEST,
  USER_LOGIN_SUCCESS,
  USER_LOGOUT,
  USER_REGIST_FAIL,
  USER_REGIST_REQUEST,
  USER_REGIST_SUCCESS,
  USER_UPDATE_FAIL,
  USER_UPDATE_REQUEST,
  USER_UPDATE_SUCCESS,
} from "../Constant/userConstant";

export const userLoginReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_LOGIN_REQUEST:
      return {
        loadingLogin: false,
      };
    case USER_LOGIN_SUCCESS:
      return {
        loadingLogin: false,
        userInfo: action.payload,
      };
    case USER_LOGIN_FAIL:
      return {
        loadingLogin: false,
        error: action.payload,
      };
    case USER_LOGOUT:
      return {};
    default:
      return state;
  }
};
export const userRegistReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_REGIST_REQUEST:
      return {
        loading: true,
      };
    case USER_REGIST_SUCCESS:
      return {
        loading: false,
        userInfo: action.payload,
      };
    case USER_REGIST_FAIL:
      return {
        loading: false,
        error: action.payload,
      };
    default:
      return state;
  }
};

export const userUpdateReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_UPDATE_REQUEST:
      return {
        loading: true,
      };
    case USER_UPDATE_SUCCESS:
      return {
        loading: false,
        userInfo: action.payload,
      };
    case USER_UPDATE_FAIL:
      return {
        loading: false,
        error: action.payload,
      };
    default:
      return state;
  }
};
