import {
  NOTES_CREATE_FAIL,
  NOTES_CREATE_REQUEST,
  NOTES_CREATE_SUCCESS,
  NOTES_DELETE_FAIL,
  NOTES_DELETE_MSG,
  NOTES_DELETE_REQUEST,
  NOTES_DELETE_SUCCESS,
  NOTES_LIST_FAIL,
  NOTES_LIST_REQUEST,
  NOTES_LIST_SUCCESS,
  NOTES_UPDATE_FAIL,
  NOTES_UPDATE_REQUEST,
  NOTES_UPDATE_SUCCESSS,
} from "../Constant/notesConstant";

export const noteListReducer = (state = { notes: [] }, action) => {
  switch (action.type) {
    case NOTES_LIST_REQUEST:
      return {
        loading: true,
      };
    case NOTES_LIST_SUCCESS:
      return {
        loading: false,
        notes: action.payload,
      };
    case NOTES_LIST_FAIL:
      return {
        loading: false,
        error: action.payload,
      };
    default:
      return state;
  }
};

export const createNotesReducer = (state = {}, action) => {
  switch (action.type) {
    case NOTES_CREATE_REQUEST:
      return {
        loading: true,
        success: false,
      };
    case NOTES_CREATE_SUCCESS:
      return {
        loading: false,
        notes: action.payload,
        success: true,
      };
    case NOTES_CREATE_FAIL:
      return {
        loading: false,
        error: action.payload,
        success: false,
      };
    default:
      return state;
  }
};
export const updateNotesReducer = (state = {}, action) => {
  switch (action.type) {
    case NOTES_UPDATE_REQUEST:
      return {
        loading: true,
      };
    case NOTES_UPDATE_SUCCESSS:
      return {
        loading: false,
        notes: action.payload,
        success: true,
      };
    case NOTES_UPDATE_FAIL:
      return {
        loading: false,
        error: action.payload,
        sucess: false,
      };
    default:
      return state;
  }
};

export const deleteNotesReducer = (state = {}, action) => {
  switch (action.type) {
    case NOTES_DELETE_REQUEST:
      return {
        loading: true,
      };
    case NOTES_DELETE_SUCCESS:
      return {
        loading: false,
        isSuccess: true,
      };
    case NOTES_DELETE_MSG:
      return {
        isSuccess: false,
        loading: false,
      };
    case NOTES_DELETE_FAIL:
      return {
        loading: false,
        error: action.payload,
        isSucess: false,
      };
    default:
      return state;
  }
};
